import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { RootState } from "../../app/store";

var id = 0;

export interface ToastState {
  myArray: MyToastType[];
  // value: boolean;
  // heading: string;
  // smallHeading: string;
  // body: string;
  // bg: toastBgEnum;
  // delay: number;
}

export enum toastBgEnum {
  true = "light",
  wrong = "danger",
}

const initialState: ToastState = {
  myArray: [],
  // value: false,
  // heading: "",
  // smallHeading: "",
  // body: "",
  // bg: toastBgEnum.true,
  // delay: 0,
};

export const toastSlice = createSlice({
  name: "toast",
  initialState,
  reducers: {
    Add: (state, action: PayloadAction<MyToastType>) => {
      state.myArray.push(
        new MyToastType(
          action.payload.varHeading,
          action.payload.varSmallHeading,
          action.payload.varBody,
          action.payload.varBg,
          action.payload.varDelay
        )
      );
      id++;
      // state.value = true;
      // state.heading = action.payload.varHeading;
      // state.smallHeading = action.payload.varSmallHeading;
      // state.body = action.payload.varBody;
      // state.bg = action.payload.varBg;
      // state.delay = action.payload.varDelay;
    },
    Remove: (state, action: PayloadAction<MyToastType>) => {
      //   for (let i = 0; i < state.myArray.length; i++) {
      //     if (state.myArray[i].varId == action.payload.varId) {
      //       delete state.myArray[i];
      //       break;
      //     }
      //   }
      // },
      // action.payload.varValue = false;
      // state.myArray[action.payload.varId].varValue = false;
      state.myArray = state.myArray.filter(checkId, action.payload.varId);
    },
  },
});

function checkId(currentValue: MyToastType, id: number) {
  return currentValue.varId != id;
}

export class MyToastType {
  varId: number;
  varValue: boolean;
  varHeading: string;
  varSmallHeading: string;
  varBody: string;
  varBg: toastBgEnum;
  varDelay: number;

  constructor(
    heading: string,
    smallHeading: string,
    body: string,
    bg: toastBgEnum,
    delay: number
  ) {
    this.varValue = true;
    this.varId = id;
    this.varHeading = heading;
    this.varSmallHeading = smallHeading;
    this.varBody = body;
    this.varBg = bg;
    this.varDelay = delay;
  }
}

export const myArray = (state: RootState) => state.toastSwitch.myArray;
