import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { RootState } from "../../app/store";

export interface ModalState {
  value: boolean;
  heading: string;
  body: string;
  button1Text: string;
  button2Text: string;
  button1Show: boolean;
  button2Show: boolean;
}

const initialState: ModalState = {
  value: false,
  heading: "",
  body: "",
  button1Text: "",
  button2Text: "",
  button1Show: false,
  button2Show: false,
};

export const modalSlice = createSlice({
  name: "modal",
  initialState,
  reducers: {
    modalOn: (state, action: PayloadAction<MyType>) => {
      state.value = true;
      state.heading = action.payload.varHeading;
      state.body = action.payload.varBody;
      state.button1Text = action.payload.varButton1Text;
      state.button2Text = action.payload.varButton2Text;
      state.button1Show = action.payload.varButton1Show;
      state.button2Show = action.payload.varButton2Show;
    },
    modalOff: (state) => {
      state.value = false;
    },
  },
});

export class MyType {
  varHeading: string;
  varBody: string;
  varButton1Text: string;
  varButton2Text: string;
  varButton1Show: boolean;
  varButton2Show: boolean;

  constructor(
    heading: string,
    body: string,
    button1Text: string,
    button2Text: string,
    button1show: boolean,
    button2show: boolean
  ) {
    this.varHeading = heading;
    this.varBody = body;
    this.varButton1Text = button1Text;
    this.varButton2Text = button2Text;
    this.varButton1Show = button1show;
    this.varButton2Show = button2show;
  }
}

export const selectModal = (state: RootState) => state.modalSwitch.value;
export const modalHeading = (state: RootState) => state.modalSwitch.heading;
export const modalBody = (state: RootState) => state.modalSwitch.body;
export const modalButton1Text = (state: RootState) =>
  state.modalSwitch.button1Text;
export const modalButton2Text = (state: RootState) =>
  state.modalSwitch.button2Text;
export const modalButton1Show = (state: RootState) =>
  state.modalSwitch.button1Show;
export const modalButton2Show = (state: RootState) =>
  state.modalSwitch.button2Show;

export const { modalOn, modalOff } = modalSlice.actions;

export default modalSlice.reducer;
