import React, { useState } from "react";
import File1 from "./CompFiles/reduxFile1";
import reduxStyles from "../src/CompFiles/reduxStyles.module.scss";

import File2 from "./CompFiles/reduxFile2";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import toastStyle from "./toastStyle.module.scss";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { useAppSelector, useAppDispatch } from "./app/hooks";
import {
  selectModal,
  modalOff,
  modalHeading,
  modalBody,
  modalButton1Text,
  modalButton2Text,
  modalButton1Show,
  modalButton2Show,
} from "./features/counter/modalSlice";
// toast
import File3 from "./CompFiles/reduxFile3";
import Toast from "react-bootstrap/Toast";
import ToastContainer from "react-bootstrap/ToastContainer";
import { Remove, myArray, MyToastType } from "./features/counter/toastSlice";

export function ToastFunction() {
  // const toastSituation = useAppSelector(selectToast);
  // const heading = useAppSelector(toastHeading);
  // const smallHeading = useAppSelector(toastSmallHeading);
  // const body = useAppSelector(toastBody);
  // const myBg = useAppSelector(toastBg);
  // const myDelay = useAppSelector(toastDelay);
  const toastArray = useAppSelector(myArray);
  const dispatch = useAppDispatch();

  // const [show, setShow] = useState(true);

  return (
    <div className={toastStyle.theContainer}>
      <Row>
        <Col xs={4}>
          <ToastContainer position="top-end" className="p-3">
            {toastArray.map((element: MyToastType) => (
              <Toast
                onClose={() => {
                  element.varValue = false;
                  dispatch(Remove(element));
                }}
                show={element.varValue}
                delay={element.varDelay}
                autohide
                bg={element.varBg.toString()}
              >
                <Toast.Header>
                  <strong className="me-auto">{element.varHeading}</strong>
                  <small className="text-muted">
                    {element.varSmallHeading}
                  </small>
                </Toast.Header>
                <Toast.Body>{element.varBody}</Toast.Body>
              </Toast>
            ))}
          </ToastContainer>
        </Col>
      </Row>
    </div>
  );
}

export default function LearnRedux() {
  return (
    <div>
      <ModalFunction />

      <File1 />
      <File2 />
      <ToastFunction />

      <div className={reduxStyles.file1}>
        <File3 />
      </div>
    </div>
  );
}

export function ModalFunction() {
  // const [show, setShow] = useState(false);
  const modalSituation = useAppSelector(selectModal);
  const heading = useAppSelector(modalHeading);
  const body = useAppSelector(modalBody);
  const button1Text = useAppSelector(modalButton1Text);
  const button2Text = useAppSelector(modalButton2Text);
  const button1Show = useAppSelector(modalButton1Show);
  const button2Show = useAppSelector(modalButton2Show);
  const dispatch = useAppDispatch();

  // const handleClose = () => setShow(false);
  // const handleShow = () => setShow(true);
  // useEffect(() => {
  //   if (modalSituation === true) handleShow();
  //   else handleClose();
  // });
  return (
    <div>
      {/* <p>its {modalSituation.toString()}</p> */}
      <Modal show={modalSituation} onHide={() => dispatch(modalOff())}>
        <Modal.Header closeButton>
          <Modal.Title>{heading}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{body}</Modal.Body>
        <Modal.Footer>
          {button2Show ? (
            <Button
              variant="secondary"
              onClick={() => dispatch(modalOff())}
              disabled={!button2Show}
            >
              {button2Text}
            </Button>
          ) : null}
          {button1Show ? (
            <Button
              variant="primary"
              onClick={() => dispatch(modalOff())}
              disabled={!button1Show}
            >
              {button1Text}
            </Button>
          ) : null}
        </Modal.Footer>
      </Modal>
    </div>
  );
}
