import reduxStyles from "./reduxStyles.module.scss";
import {
  Add,
  MyToastType,
  toastBgEnum,
} from ".././features/counter/toastSlice";
import { useAppDispatch } from "../app/hooks";

export default function ReduxFile1() {
  const dispatch = useAppDispatch();

  return (
    <div>
      <button
        onClick={() => {
          dispatch(
            Add(
              new MyToastType(
                "heading1",
                "smallHeading1",
                "body1",
                toastBgEnum.true,
                1000
              )
            )
          );
        }}
        className={reduxStyles.button}
      >
        Toast True
      </button>
      <button
        onClick={() => {
          dispatch(
            Add(
              new MyToastType(
                "heading2",
                "smallHeading2",
                "body2",
                toastBgEnum.wrong,
                2000
              )
            )
          );
        }}
        className={reduxStyles.button}
      >
        Toast False
      </button>
    </div>
  );
}
